import * as React from 'react';
import Card from "./card"
import "../../style/slider.scss"


const Slider = (props) =>
{
    const slider = React.useRef();
    const sliderBody = React.useRef();
    let deltaW = 0
    const onMouseWheel = (event) =>
    {
        let rightLim = slider.current.getBoundingClientRect().width - sliderBody.current.getBoundingClientRect().width;
        const delta = Math.sign(event.deltaY);
        deltaW = -delta * 130 + +slider.current.style.marginLeft.slice(0, -2);
        if (deltaW > 0 || deltaW <= -rightLim - 40)
        {
            return null
        }
        else
        {
            requestAnimationFrame(() =>
            {
                slider.current.style.marginLeft = `${deltaW}px`;
            })
        }
    }
    React.useEffect(() =>
    {
        slider.current.addEventListener("wheel", onMouseWheel);
        return () =>
        {
            slider.current.removeEventListener("wheel", onMouseWheel);
        }
    }, [])

    return (
        <div>
            <div ref={sliderBody} className="slider-main">
                <div className="slider-body" ref={slider} style={{ transition: `all .6s ease-out` }}>
                    {props.cards.map((item, index) => (
                        <Card key={index} data={item} />
                    )
                    )}
                </div>
            </div>
            <div className="flex-between slider-pages">
                <div>...................</div>
                <div>scroll</div>
            </div>
        </div>
    );
}

export default Slider;